
.proverbBox {
    transition: all 300ms ease; 
    border: 2px;
    border-color: #777;
    border-style: solid;
    width: 98%;
    padding: 5px;
    margin: 5px;
    box-sizing: border-box;
    align-items: center;
    align-content: center;
    justify-content: center;
    text-align: center;
  }
  
  .proverb {
    width: 70%;
    display: block;
    margin: auto;
    padding: 10px;
    text-align: left;
  }
  
  .proverb-text {
    display: block;
    margin: auto;
    font-weight: bold;
    text-align: left;
    font-family: cursive;
    /* font-size: 22px; */
    font-style: italic;
    font-variant: normal;
    /* line-height: 22px; */
  }
  
  .proverb-author {
    display: block;
    margin: auto;
    text-align: right;
    font-style: italic;
  }

  blockquote {
    transition: all 300ms ease; 
      font-family: Georgia, serif;
      font-size: 22px;
      font-style: italic;
      /* width: 500px; */
      /* width: 80%; */
      margin: 0.25em 0;
      padding: 0.25em 40px;
      line-height: 1.45;
      position: relative;
      color: #383838;
  }
  
  blockquote:before {
    transition: all 300ms ease; 
      display: block;
      content: "\201C";
      font-size: 100px;
      position: absolute;
      left: -20px;
      top: -20px;
      color: #7a7a7a;
  }

  blockquote cite {
    transition: all 300ms ease; 
      color: #999999;
      font-size: 16px;
      display: block;
      margin-top: 5px;
  }
  
  blockquote cite:before {
      content: "\2014 \2009";
  }

@media screen and (max-width: 700px) {
    blockquote {
        font-size: 18px;
        margin: 0.25em 0;
        padding: 0.25em 20px;
        line-height: 1.15;
    }
  
    blockquote:before {
        content: "\201C";
        font-size: 40px;
        left: -10px;
        top: -10px;
    }
  
    blockquote cite {
        font-size: 12px;
        margin-top: 3px;
    }
}