html {
    height: 100%;
}

body {
    margin: 0;
    padding: 0;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
        "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
        sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    height: 100%;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
        monospace;
}

  .Archive-thumb {
    max-width: 40%;
    margin: 5px;
    /* min-height: 60px; */
  }
.ContactFormMessage {
    width: 500px;
    min-width: 300px;
}

.ContactFormSend {
    display: block;
    background-color: #555;
    border-radius: 10px;
}

/* Reference for form design: https://www.sanwebe.com/2014/08/css-html-forms-designs */

.ContactForm {
	font-family: 'Open Sans Condensed', arial, sans;
	width: 500px;
	padding: 30px;
	background: #FFFFFF;
	margin: 50px auto;
	box-shadow: 0px 0px 15px rgba(0, 0, 0, 0.22);
	-moz-box-shadow: 0px 0px 15px rgba(0, 0, 0, 0.22);
	-webkit-box-shadow:  0px 0px 15px rgba(0, 0, 0, 0.22);
}

@media screen and (max-width: 700px) {
    .ContactForm {
        width: 300px;
    }
}

.ContactForm h2{
	background: #4D4D4D;
	text-transform: uppercase;
	font-family: 'Open Sans Condensed', sans-serif;
	color: #797979;
	font-size: 18px;
	font-weight: 100;
	padding: 20px;
	margin: -30px -30px 30px -30px;
}
.ContactForm input[type="text"],
.ContactForm input[type="date"],
.ContactForm input[type="datetime"],
.ContactForm input[type="email"],
.ContactForm input[type="number"],
.ContactForm input[type="search"],
.ContactForm input[type="time"],
.ContactForm input[type="url"],
.ContactForm input[type="password"],
.ContactForm textarea,
.ContactForm select 
{
	box-sizing: border-box;
	-webkit-box-sizing: border-box;
	-moz-box-sizing: border-box;
	outline: none;
	display: block;
	width: 100%;
	padding: 7px;
	border: none;
	border-bottom: 1px solid #ddd;
	background: transparent;
	margin-bottom: 10px;
	font: 16px Arial, Helvetica, sans-serif;
}
.ContactForm textarea{
	resize:none;
	overflow: hidden;
}
.ContactForm input[type="button"], 
.ContactForm input[type="submit"],
.ContactFormButton {
	box-shadow: inset 0px 1px 0px 0px #45D6D6;
	background-color: #2CBBBB;
	border: 1px solid #27A0A0;
	display: inline-block;
	cursor: pointer;
	color: #FFFFFF;
	font-family: 'Open Sans Condensed', sans-serif;
	font-size: 14px;
	padding: 8px 18px;
	text-decoration: none;
	text-transform: uppercase;
}
.ContactForm input[type="button"]:hover, 
.ContactForm input[type="submit"]:hover,
.ContactFormButton:hover {
	background:linear-gradient(to bottom, #34CACA 5%, #30C9C9 100%);
	background-color:#34CACA;
}

.shareButton {
    cursor: pointer;
}

.shareButton:hover:not(:active) {
    opacity: 0.75;
}


.proverbBox {
    transition: all 300ms ease; 
    border: 2px;
    border-color: #777;
    border-style: solid;
    width: 98%;
    padding: 5px;
    margin: 5px;
    box-sizing: border-box;
    -webkit-align-items: center;
            align-items: center;
    -webkit-align-content: center;
            align-content: center;
    -webkit-justify-content: center;
            justify-content: center;
    text-align: center;
  }
  
  .proverb {
    width: 70%;
    display: block;
    margin: auto;
    padding: 10px;
    text-align: left;
  }
  
  .proverb-text {
    display: block;
    margin: auto;
    font-weight: bold;
    text-align: left;
    font-family: cursive;
    /* font-size: 22px; */
    font-style: italic;
    -webkit-font-feature-settings: normal;
            font-feature-settings: normal;
    font-variant: normal;
    /* line-height: 22px; */
  }
  
  .proverb-author {
    display: block;
    margin: auto;
    text-align: right;
    font-style: italic;
  }

  blockquote {
    transition: all 300ms ease; 
      font-family: Georgia, serif;
      font-size: 22px;
      font-style: italic;
      /* width: 500px; */
      /* width: 80%; */
      margin: 0.25em 0;
      padding: 0.25em 40px;
      line-height: 1.45;
      position: relative;
      color: #383838;
  }
  
  blockquote:before {
    transition: all 300ms ease; 
      display: block;
      content: "\201C";
      font-size: 100px;
      position: absolute;
      left: -20px;
      top: -20px;
      color: #7a7a7a;
  }

  blockquote cite {
    transition: all 300ms ease; 
      color: #999999;
      font-size: 16px;
      display: block;
      margin-top: 5px;
  }
  
  blockquote cite:before {
      content: "\2014   \2009";
  }

@media screen and (max-width: 700px) {
    blockquote {
        font-size: 18px;
        margin: 0.25em 0;
        padding: 0.25em 20px;
        line-height: 1.15;
    }
  
    blockquote:before {
        content: "\201C";
        font-size: 40px;
        left: -10px;
        top: -10px;
    }
  
    blockquote cite {
        font-size: 12px;
        margin-top: 3px;
    }
}
